<template>
  <CRow class="justify-content-center">
    <CCol>
      <CCard>
        <CCardHeader class="d-flex justify-content-between">
          <div>
            <slot name="header">
              <CIcon name="cil-healing" />
              <strong>Purchases</strong>
            </slot>
          </div>
          <CButton
            variant="outline"
            color="success"
            @click="openModalNewSupplier()"
          >
            <CIcon name="cil-plus" class="mt-0" />&nbsp;Add New Purchase
          </CButton>
        </CCardHeader>
        <CCardBody>
          <v-client-table :data="rows" :columns="columns" :options="options">
            <div slot="actions" slot-scope="{ row }">
              <!-- <CButton
                class="mr-3"
                variant="outline"
                @click="openModalEdit(row)"
                color="primary"
              >
                Edit
                <CIcon name="cil-pencil" />
              </CButton> -->
              <CButton
                class="mr-3"
                variant="outline"
                @click="deleteRow(row)"
                color="danger"
              >
                Delete
                <CIcon name="cil-delete" />
              </CButton>
            </div>
          </v-client-table>
        </CCardBody>
      </CCard>
    </CCol>
    <sweet-modal :blocking="Boolean(true)" ref="modal" overlay-theme="dark">
      <CCard style="text-align: left; border: 1px solid">
        <CCardHeader>
          <strong>New Purchase Form</strong>
        </CCardHeader>
        <CForm @submit.prevent="submit">
          <CCardBody>
            <p
              v-for="(error, index) in errors"
              :key="index"
              class="alert alert-danger"
            >
              {{ error }}
            </p>
            <CSelect
              placeholder="Select a Supplier"
              label="Supplier name"
              @change="changeSupplier($event)"
              :options="sup"
              v-model.trim="$v.supplier.$model"
              :class="{ 'form-group--error': $v.supplier.$error }"
            />
            <div class="error" v-if="!$v.supplier.required">
              Supplier is required
            </div>
            <CInput
              id="bill"
              :class="{ 'form-group--error': $v.bill.$error }"
              label="Bill number"
              v-model.trim="$v.bill.$model"
            />
            <div class="error" v-if="!$v.bill.required">
              Bill number is required
            </div>
            <CInput
              id="description"
              label="Description"
              v-model="description"
            />
            <CRow>
              <CCol md="6">
                <CInput
                  id="total"
                  :class="{ 'form-group--error': $v.total.$error }"
                  type="number"
                  step="any"
                  label="Total"
                  v-model.trim="$v.total.$model"
                />
                <div class="error" v-if="!$v.total.required">
                  Total is required
                </div>
              </CCol>
              <CCol md="6">
                <CInput
                  id="date"
                  :class="{ 'form-group--error': $v.date.$error }"
                  type="date"
                  label="Date"
                  v-model.trim="$v.date.$model"
                />
                <div class="error" v-if="!$v.date.required">
                  Date is required
                </div>
                <div class="error" v-if="!$v.date.maxValue">
                  Date cant be a future date
                </div>
              </CCol>
            </CRow>
          </CCardBody>
          <CCardFooter>
            <CButton
              class="col"
              variant="outline"
              color="success"
              type="submit"
            >
              <CIcon name="cil-check-circle" />&nbsp;Submit
            </CButton>
          </CCardFooter>
        </CForm>
      </CCard>
    </sweet-modal>
  </CRow>
</template>
<script>
import moment from "moment";
import { required } from "vuelidate/lib/validators";
export default {
  name: "Purchases",
  data() {
    return {
      supplier: "",
      total: "",
      description: "",
      date: "",
      sup: [],
      errors: [],
      bill: "",
      paid: "",
      id: "",
      contact: "",
      isEdit: false,
      amount: "",
      rows: [],
      columns: [
        "bill_number",
        "supplier.name",
        "date",
        "description",
        "total",
        "actions",
      ],
      options: {
        columnsClasses: {
          actions: "text-center",
          total: "text-center",
          description: "text-center",
        },
        filterByColumn: true,
        perPage: 10,
        texts: {
          filter: "Filter:",
          filterBy: "Filter by {column}",
          count: " ",
        },
        filterable: ["bill_number"],
        headings: {
          name: "Name",
          id: "ID",
          "supplier.name": "Supplier",
        },
      },
    };
  },
  validations: {
    supplier: {
      required,
    },
    date: {
      required,
      maxValue: value => value < new Date().toISOString()
    },
    bill: {
      required,
    },
    total: {
      required,
    },
  },
  mounted() {
    this.getData();
    this.getSupplier();
  },
  methods: {
    submit() {
      this.$v.$touch();
      if (this.$v.$invalid) {
        this.errors.push("Please check the form and submit.");
      } else {
        this.formData = {
          supplier: this.supplier,
          bill_number: this.bill,
          date: this.date,
          description: this.description,
          total: this.total,
        };
        if (this.isEdit === false) {
          this.$http
            .post("http://localhost:3000/purchase/post", this.formData)
            .then((res) => {
              this.$swal.fire("Done!", "Purchase created successfully!", "success");
              this.getData();
            })
            .catch((err) => {
              // eslint-disable-next-line no-console
              this.$swal.fire("Values have errors", err.response.data.errors[0].msg || "Something went wrong.", "error");
              console.log(err.response);
            });
        } else {
          this.$http
            .put("http://localhost:3000/purchase/" + this.id, this.formData)
            .then((res) => {
              this.$swal.fire("Done!", "Purchase edited successfully!", "success");
              this.getData();
            })
            .catch((err) => {
              // eslint-disable-next-line no-console
              this.$swal.fire("Values have errors", err.response.data.errors[0].msg || "Something went wrong.", "error");
              console.log(err.response);
            });
        }
        this.$refs.modal.close("modal");
      }
    },
    changeSupplier(e) {
      this.supplier = e.target.value;
    },
    getData() {
      this.$http
        .get("http://localhost:3000/purchase/get")
        .then((res) => {
          res.data.forEach((item) => {
            let local = moment.utc(item.date).local().format("YYYY-MM-DD");
            item.date = local;
          });
          this.rows = res.data;
          console.log(res.data);
        })
        .catch((err) => {
          // eslint-disable-next-line no-console
          console.log(err.response);
        });
    },
    getSupplier() {
      this.$http
        .get("http://localhost:3000/supplier/get")
        .then((res) => {
          let array = [];
          res.data.forEach((item) => {
            array.push(item.name);
          });
          this.sup = array;
        })
        .catch((err) => {
          // eslint-disable-next-line no-console
          console.log(err.response);
        });
    },
    deleteRow(row) {
      this.$http
        .delete("http://localhost:3000/purchase/" + row._id)
        .then((res) => {
          this.getData();
        })
        .catch((err) => {
          // eslint-disable-next-line no-console
          console.log(err.response);
        });
    },
    async openModalEdit(row) {
      console.log(row);
      let local = moment.utc(row.date).local().format("YYYY-MM-DD");
      console.log(local);
      this.isEdit = true;
      this.id = row._id;
      this.$refs.modal.open("modal");
      this.supplier = row.supplier.name;
      this.bill = row.bill_number;
      this.date = local;
      this.description = row.description;
      this.total = row.total;
      this.errors = []
    },
    async openModalNewSupplier() {
      this.isEdit = false;
      this.$refs.modal.open("modal");
      this.supplier = "";
      this.bill = "";
      this.date = "";
      this.description = "";
      this.total = "";
      this.errors = []
    },
  },
};
</script>